@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i|Raleway:300,300i,400,400i,500,500i,600,600i,700,700i|Poppins:300,300i,400,400i,500,500i,600,600i,700,700i|'https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&family=Nunito&display=swap');
/* Google Fonts */

:root {
  --white-color: rgb(255, 255, 255);
  --black-color: #000;
  --heading-font: "Raleway", sans-serif;
  --heading-font2: "Poppins", sans-serif;
  --paragraph-font: "Montserrat", sans-serif;
  --dark-font-color: #fd8d03;
  --light-font-color: #ffd002;
  --light-blue-background: #f9cd0b;
  --smallest-font: 14px;
  --section-padding-top-bottom: 15px;
  --section-title-padding-bottom: 30px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #272829;
}

a {
  color: #f9cd0b;
  color: var(--light-blue-background);
  text-decoration: none !important;
}

a:hover {
  color: #edc61b;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
  font-family: var(--heading-font);
}
h3 {
  font-weight: 700 !important; 
  font-size: 26px ;
}
h4{
  font-size: 18px !important;
}
h5{
  font-size: 16px !important;
}
p{
  font-family: "Montserrat", sans-serif;
  font-family: var(--paragraph-font);
}

section {
  padding: 15px 0;
  padding: var(--section-padding-top-bottom) 0;
  padding-bottom: 0;
  /* border: 2px solid salmon; */
  overflow: hidden;
}

.section-bg {
  background: #f5f8fd;
}

.section-title {
  padding-bottom: 30px;
  padding-bottom: var(--section-title-padding-bottom);
}
.section-title h2 {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 5px;
  padding-bottom: 10px;
  position: relative;
  color: #ffd002;
  color: var(--light-font-color);
}
.section-title h2::after {
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 3px;
  background: #f9cd0b;
  background: var(--light-blue-background);
  bottom: 0;
  left: 0;
}
.section-title p {
  margin-bottom: 0;
}
.btn-container{
  display: flex;
  overflow-x: auto;
}

 .btn-container::-webkit-scrollbar{
  width: 0;
}

.btn-container button{
  background: none;
  color: #000;
  color: var(--black-color);
  border: 2px solid #f9cd0b;
  border: 2px solid var(--light-blue-background);
  padding: 0.3rem 1rem;
  margin-right: 1.3rem;
  margin-bottom: 1rem;
  border-radius: 1.5rem;
  font-size: 14px;
  font-size: var(--smallest-font);  
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 600;
  white-space: nowrap;
  transition: all 0.2s ease-in-out;
}
.btn-container .active, .btn-container button:hover{
  color: rgb(255, 255, 255) !important;
  color: var(--white-color) !important;
  background-color: #f9cd0b !important;
  background-color: var(--light-blue-background) !important;
}

@media only screen and (max-width: 768px){
  :root{
    --smallest-font: 10px;
  }
  .section-title h2{
    font-size: 30px;
  }
  p{
    font-size: 14px;
  }
  :root{
    --section-padding-top-bottom: 10px;
  }
}
.header-section {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  transition: all ease-in-out 0.5s;
  z-index: 9997;
  padding: 0 15px;
  background: #040b14;
  overflow-y: auto;
}

@media (max-width: 1000px) {
  .header-section {
    left: -300px;
  }
}


/*--------------------------------------------------------------
# Navigation Menu
--------------------------------------------------------------*/

/* Desktop Navigation */
.nav-menu {
  padding: 30px 0 0 0;
}
.nav-menu * {
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav-menu > ul > li {
  position: relative;
  white-space: nowrap;

}
.nav-menu .nav_link {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  width: 100%;
  align-items: flex-end;
  padding: 12px 6px;
  margin-bottom: 0px;
  margin-top: 1px;
  margin-left: 10px;
  transition: 0.3s;
  font-size: 18px;
  color: #a8a9b4;
  cursor: pointer;
}

.nav-menu .nav_link .nav_icon{
  padding-bottom: 1px;
}

.nav-active{
  left:0
}

.nav-menu .icon{
  color: #6f7180;
  font-size: 26px;
  text-decoration: none;
}

.nav_name{
  text-transform: capitalize;
}

.nav-menu .nav_link .nav_name:hover, .nav-menu .active .nav_name{
  text-decoration: none;
  color: var(--light-blue-background);
}

.nav-menu .active .icon {
  color: var(--light-blue-background);
  text-decoration: none;
}

.nav-wrapper{
  padding-bottom: 25px;
}

/* Mobile Navigation */
.mobile-nav-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  z-index: 9998;
  border: 0;
  font-size: 26px;
  transition: all 0.4s;
  outline: none !important;
  background-color: var(--light-blue-background);
  color: var(--white-color);
  width: 40px;
  height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 0;
  border-radius: 50%;
  cursor: pointer;
}

@media only screen and (max-width: 768px){
  .nav-menu .nav_link{
    font-size: 16px;
    grid-gap: 2px;
    gap: 2px;
    padding: 8px 6px;
  }
  .nav-menu .icon{
    font-size: 22px;
  }

  
}
.nav-menu .nav_scroll_link{
  text-decoration: none;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.274);
  overflow: hidden
}
.header-section .profile img {
  margin: 15px auto;
  display: block;
  width: 150px;
  height: 150px;
  border: 7px solid #2c2f3f;
}

.header-section .profile h1 {
  font-size: 30px;
  margin: 0;
  padding: 0;
  font-weight: 600;
  text-align-last: center;
  font-family: var(--heading-font2);
  color: var(--white-color);
}

.header-section .profile .social-links a {
  font-size: 18px;
  display: inline-block;
  background: #212431;
  color: var(--white-color);
  line-height: 1;
  padding: 9px 0;
  margin-top: 0;
  margin-right: 6px;
  border-radius: 50%;
  text-align: center;
  width: 42px;
  height: 42px;
  transition: 0.2s;
}
.header-section .profile .social-links a:hover {
  background: var(--light-blue-background);
  color: var(--white-color);
  text-decoration: none;
}

@media only screen and (max-width: 768px){
  .header-section .profile .social-links a{
    font-size: 16px;
    width: 40px;
    height: 40px;
  }
}
.main {
  margin-left: 300px;
}

 /* To Fix Warning in Edge */

body {
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
          text-size-adjust: 100%;
}

.form-control {
  --webkit-background-clip: padding-box;
}

.form-check-input {
  -webkit-print-color-adjust: exact;
}

.sticky-top {
  position: -webkit-sticky;
}

th {
  text-align: match-parent;
  text-align:-webkit-match-parent
}

@media (max-width: 1199px) {
  .main {
    margin-left: 0;
  }
}
.contact{
  padding-bottom: 0px;
}

.contact .info {
  padding: 30px;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.contact .address{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.contact .contact_box{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 20%;
}

.contact .address_text{
  margin-left: 10px;
  font-weight: 500;
}

.contact .contact_box_text{
  margin-right: 10px;
  font-weight: 500;
}

.contact .info .contact_icon {
  font-size: 25px;
  padding: 9px;
  background: var(--light-blue-background);
  color: var(--white-color);
  width: 40px;
  height: 40px;
  border-radius: 50px;
  transition: all 0.3s ease-in-out;
}


@media only screen and (max-width: 768px){
  .contact .info{
   flex-direction: column;
  }
  .contact .contact_box{
    margin-top: 30px;
    width: 50%;
    margin-left: 0;
  }
  .contact .address{
    width: 100%;
  }
}

.portfolio .portfolio-flters {
  padding: 0;
  margin: 0 auto 10px auto;
  list-style: none;
  text-align: center;
  background: var(--white-color);
  border-radius: 50px;
  padding: 2px 15px;
}

.portfolio .portfolioCard-wrapper{
  padding-top: 0;
}

.portfolio .subcategory-wrapper{
  padding-top: 0;
}

.portfolio .portfolio-flters li {
  cursor: pointer;
  display: inline-block;
  padding: 10px 15px 8px 15px;
  font-size: var(--smallest-font);
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
  color: #272829;
  transition: all 0.2s ease-in-out;
}
.portfolio .portfolio-flters li:hover, .portfolio .portfolio-flters li.filter-active {
  color: var(--light-blue-background);
}
.portfolio .portfolio-flters li:last-child {
  margin-right: 0;
}


.portfolioCard_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1200px;
}

.portfolioCard_container .portfolioCard_card {
  height: 440px;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
    inset -5px -5px 15px rgba(255, 255, 255, 0.1),
    5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  margin: 20px;
  transition: 0.5s ;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.portfolioCard_container .portfolioCard_card .portfolioCard_box {
  margin: 20px;
  height: 100%;
  background: #343A40;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 300px;
}

.portfolioCard_container .portfolioCard_card:hover {
  box-shadow: inset 5px 5px 5px rgba(221, 204, 20, 0.3),
  inset -5px -5px 15px rgba(221, 184, 20, 0.1),
  5px 5px 15px rgba(221, 188, 20, 0.3), -5px -5px 15px rgba(20, 157, 221, 0.1);
}


.portfolioCard_container .portfolioCard_card .portfolioCard_box .portfolioCard_content {
  padding: 20px;
  text-align: center;
  margin: 0;
  width: 100%;
  height: 100%;
}


.portfolioCard_container .portfolioCard_card .portfolioCard_box .portfolioCard_content h3 {
  font-size: 1rem;
  color: var(--white-color);
  z-index: 1;
  transition: 0.5s;
  margin-top: 1px;
  margin-bottom: 2px;
  text-transform: uppercase;
}

.portfolioCard_container .portfolioCard_card .portfolioCard_box .portfolioCard_content a:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: var(--white-color);
  color: #2196f3;
}

.portfolioCard_card .portfolioCard_box .portfolioCard_content p{
  height: 320px;
  padding-top: 10px;
  padding-right: 0px;
  margin-left: 0px;
  margin-top: 3px;
  z-index: 1;
  transition: 0.5s;

}
.portfolioCard_card .portfolioCard_box .portfolioCard_content p img{
  height: 100%;
  width: 100%;
  border-radius: 6px;
}



@media only screen and (max-width: 768px){
  .portfolioCard_container{
    display: block;
    margin: 5px 5px;
  }
  .portfolioCard_container .portfolioCard_card{
    height: 440px;
    box-shadow: none;
    margin: 0;
    width: 100%;
    position: relative;
  }
  .portfolioCard_container .portfolioCard_card .portfolioCard_box{
    width: 90%;
    min-width: 300px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-56%, -56%);
            transform: translate(-56%, -56%);
  }
  .portfolioCard_container .portfolioCard_card .portfolioCard_box{
    height: auto;
  }
  .portfolioCard_container .portfolioCard_card:hover{
    box-shadow: none;
  }
}





